// 用于定义发起网络请求的方法
// 每个方法只负责一个URL地址
import request from "@/utils/request";
import store from "@/store";

// 用户注册
export const registerAPI = (date) => {
  return request({
    url: "/manager/regist",
    method: "POST",
    data: date,
  });
};
// 获取注册食堂列表
export const getRoomListAPI = () => {
  return request({
    url: "/manager/roomlist",
    method: "POST",
  });
};
// 用户登录
export const loginAPI = (data) => {
  return request({
    url: "/user/login",
    method: "post",
    data,
  });
};
// 获取用户信息
export const getUserInfoAPI = () => {
  return request({
    url: "/user/info",
    headers: {
      Authorization: store.state.token,
    },
  });
};
// 获取店铺信息
export const getShopInfoAPI = () => {
  return request({
    url: "/shop/info",
    headers: {
      Authorization: store.state.token,
    },
  });
};
